<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <!-- 搜索表单 start -->
        <el-form
          :inline="true"
          :model="searchValidateForm"
          ref="searchValidateForm"
          label-width="100px"
          class="search-validate-form"
          :rules="searchValidateFormRules"
        >
          <!-- 请选择公司 start -->
          <el-form-item label="" prop="companyId">
            <el-select
              v-model="searchValidateForm.companyId"
              filterable
              placeholder="请选择公司"
              clearable
              @change="onChangeSearchCompany"
            >
              <el-option
                v-for="(item, index) in searchCompanyOptions"
                :key="index"
                :label="item.COMPANY_NAME"
                :value="item.COMPANY_ID"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择公司 end -->

          <!-- 请选择景区 start -->
          <el-form-item label="" prop="scenicSpotId">
            <el-select
              v-model="searchValidateForm.scenicSpotId"
              filterable
              placeholder="请选择景区"
              clearable
              @change="onChangeSearchSpot"
            >
              <el-option
                v-for="(item, index) in searchScenicOptions"
                :key="index"
                :label="item.SPOT_NAME"
                :value="item.SPOT_ID"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择景区 end -->

          <!-- 请选择日期 start -->
          <el-form-item label="" prop="date">
            <el-date-picker
              v-model="searchValidateForm.date"
              type="month"
              placeholder="选择年-月"
              value-format="yyyy-MM"
            >
            </el-date-picker>
          </el-form-item>
          <!-- 请选择日期 end -->

          <!-- 输入目标金额 start -->
          <el-form-item label="" prop="targetAmount">
            <el-input
              type="number"
              class="input-name"
              v-model="searchValidateForm.targetAmount"
              placeholder="目标金额"
            ></el-input>
          </el-form-item>
          <!-- 输入目标金额 end -->
        </el-form>
        <!-- 搜索表单 end -->

        <!-- 搜索 start -->
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="onSearch('searchValidateForm')"
          >搜索</el-button
        >
        <!-- 搜索 end -->

        <!-- 重置 start -->
        <el-button type="primary" icon="el-icon-refresh-right" @click="onReset"
          >重置</el-button
        >
        <!-- 重置 end -->

        <!-- 新增 start -->
        <el-button
          type="primary"
          icon="el-icon-circle-plus-outline"
          @click="onAdd"
          >添加</el-button
        >
        <!-- 新增 end -->

        <!-- 导出 Excel start -->
        <el-button
          type="primary"
          :loading="downloadLoading"
          icon="el-icon-download"
          @click="exportExcel"
          >导出Excel</el-button
        >

        <!-- 隐藏的a标签，用来导出excel -->
        <a id="exportA" class="exportA" :href="downloadUrl" download></a>
        <!-- 导出 Excel end -->

        <!-- 导入EXCEL start -->
        <el-upload
