              v-for="(value, key) in tableFields"
              :key="key"
              :prop="value"
              :label="key"
              align="center"
              min-width="130"
            >
              <template slot-scope="scope">
                <div>
                  {{ tableContentValue(scope.row, value) }}
                </div>
              </template>
            </el-table-column>

            <el-table-column label="操作" align="center" min-width="120">
              <template slot-scope="scope">
                <el-button
                  @click.native.prevent="detailRow(scope.$index, scope.row)"
