      this.validateForm.robotCost = res.data.robotCost

      var robotCostListIsEmpty = true
      if (res.data.robotCostList?.length !== 0) {
        res.data.robotCostList.forEach((item) => {
          if (item) {
            robotCostListIsEmpty = false
          }
        })
      }
      if (!robotCostListIsEmpty) {
        this.validateForm.robotCostList = res.data.robotCostList
      }

      // 维养成本
      this.validateForm.maintainCost = res.data.maintainCost

      var maintainCostListIsEmpty = true
      if (res.data.maintainCostList?.length !== 0) {
        res.data.maintainCostList.forEach((item) => {
          if (item) {
