      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // dialog提交
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // 运营人员成本
          var operateCost = Number(this.validateForm.operateCost)

          // 总的运营人员成本
          var operateCostSum = this.validateForm.operateCostList.reduce((res, item) => res + Number(item.commitmentAmount), 0)

          // 景区营销成本
          var spotMarketCost = Number(this.validateForm.spotMarketCost)

          // 总的景区营销成本
          var spotMarketCostSum = this.validateForm.stopMarketCostList.reduce((res, item) => res + Number(item.commitmentAmount), 0)

          if(operateCost != operateCostSum) {
            this.$message({
              message: '请输入正确的运营人员成本',
              type: 'error',
            })
            return
          }

          console.log("spotMarketCost: ", spotMarketCost);
          console.log("spotMarketCostSum: ", spotMarketCostSum);
          if(spotMarketCost != spotMarketCostSum) {
            this.$message({
              message: '请输入正确的景区营销成本',
              type: 'error',
            })
            return
          }

          if (this.dialogState == 'add') {
            // 新增公司信息
            console.log('新增公司信息')
            this.addTableData()
          } else if (this.dialogState == 'edit') {
            // 编辑公司信息
            console.log('编辑公司信息')
            this.editTableData()
          }

          this.$refs.validateForm.resetFields() // 重置表单（清空无效）
          this.getValidateForm() // 将validateForm对象中各个属性清空

          this.dialogVisible = false
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

