    //     if (
    //       value == '合作主体' ||
    //       value == 'scenicSpotName' ||
    //       value == 'robotCode'
    //     ) {
    //       return 'left'
    //     }
    //   }
    // }
    tableContentValue() {
      return (row, value) => {
        // console.log(row, value)
        var result = ''
        if (value == 'robotCost') {
          result = (row[value] && parseFloat(row[value]).toFixed(2)) || '无'
        } else {
          result = row[value] || '无'
        }
        return result
      }
    }
